
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import Tabs from '@/components/tabs/tabs';
export default {
components: {
XHeader,
InlineLoading,
Tabs
},
data() {
return {
panes: [],
dataSeeIng: [],
list: [],
pageIndex: 1,
pageSize: 100000,
conIndex: 0,
liIndex: -1,
gotoDetailIndex: 0,
newConIndex: 0,
firstSet: true,
firstTwoSet: 0, //设置二级获取top 次数
}
},
mounted() {
this.$nextTick(() => {
this.getPanes();
let gotoDetail = JSON.parse(window.localStorage.getItem('gotoSymptomDetails'));
if (gotoDetail) {
this.gotoDetailIndex = gotoDetail.index;
this.newConIndex = gotoDetail.conIndex;
}
});
},
beforeRouteLeave(to, from, next) {
if (to.name == 'drugsList') {
window.localStorage.setItem('gotoSymptomDetails', JSON.stringify({
index: this.gotoDetailIndex,
conIndex: this.newConIndex
}));
} else {
window.localStorage.removeItem('gotoSymptomDetails');
}
next();
},
updated() {
if (this.$refs.tabasss.$refs.myBoxLi && this.firstSet) {
this.firstSet = false;
this.liIndex = 0;
this.liIndex = this.gotoDetailIndex;
this.conIndex = this.newConIndex
}
},
methods: {

getPanes() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'drugpool',
'MSH.2': 'listType',
'pageIndex': this.pageIndex,
'pageSize': this.pageSize,
'parentBm': '',
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

let self = this;
for (let index = 0; index < data.rows.length; index++) {
const element = data.rows[index];
self.panes.push({
label: element.CVALUE,
code: element.CKEY,
index: index
})
}
if (this.panes.length > 0) {



this.getList(this.gotoDetailIndex);
}
}
}
})
},

getList(index) {
if (!this.list[index]) {
let self = this;
this.$ajax.post(this.$root.host, {
'MSH.1': 'drugpool',
'MSH.2': 'listType',
'pageIndex': this.pageIndex,
'pageSize': this.pageSize,
'parentBm': this.panes[index].code,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

let self = this;
this.list[index] = [];
for (let i = 0; i < data.rows.length; i++) {
const element = data.rows[i];
self.list[index].push({
label: element.CVALUE,
code: element.CKEY,
})
}
this.dataSeeIng = this.list[index];
if (this.firstTwoSet < 2) {
this.firstTwoSet++;
this.$refs.tabasss.setScrollPosition();
}
} else {
this.dataSeeIng = [];
}
} else {
this.dataSeeIng = [];
}
})
} else {
this.dataSeeIng = this.list[index];
}
},

onLiClick(i, j, obj) {
this.gotoDetailIndex = obj.index;
this.getList(obj.index);
},

onConClick(i, j, obj) {
this.newConIndex = j;
this.$router.push({
name: 'drugsList',
params: {
code: obj.code,
name: obj.label
}
});
},
searchbt() {
this.$router.push({
name: "drugsSearch",
});
}
}
}



